<template>
    <el-dialog :title="formTitle" :value="value" v-model="visible" @close="resetFormData">
        <el-form :model="formData" label-width="120px" :rules="formRules" ref="formData">
            <el-form-item label="商品名称: " prop="productName">
                {{formData.name}}
            </el-form-item>

            <el-form-item required label="样式" prop="styleId" v-if="styleList!=null">
                <el-select placeholder="请选择样式" v-model="formData.styleId">
                    <el-option :label="item.name" :value="item.id" v-for="item in styleList"></el-option>
                </el-select>
            </el-form-item>

            <el-form-item required label="尺码" prop="sizeId" v-if="sizeList!=null">
                <el-select placeholder="请选择尺码" v-model="formData.sizeId">
                    <el-option :label="item.name" :value="item.id" v-for="item in sizeList"></el-option>
                </el-select>
            </el-form-item>

            <el-form-item required label="收货人" prop="adressResponse.username">
                <el-input v-model="formData.adressResponse.username" auto-complete="off"></el-input>
            </el-form-item>

            <el-form-item required label="手机号" prop="adressResponse.phone">
                <el-input v-model="formData.adressResponse.phone" auto-complete="off"></el-input>
            </el-form-item>

            <el-form-item required label="省" prop="adressResponse.area1">
                <el-input v-model="formData.adressResponse.area1" auto-complete="off"></el-input>
            </el-form-item>

            <el-form-item required label="市" prop="adressResponse.area2">
                <el-input v-model="formData.adressResponse.area2" auto-complete="off"></el-input>
            </el-form-item>

            <el-form-item required label="区" prop="adressResponse.area3">
                <el-input v-model="formData.adressResponse.area3" auto-complete="off"></el-input>
            </el-form-item>

            <el-form-item required label="住址" prop="adressResponse.address">
                <el-input v-model="formData.adressResponse.address" auto-complete="off"></el-input>
            </el-form-item>

        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="small" @click="visible = false">取消</el-button>
            <el-button size="small" type="primary" @click.native="formSubmit" :loading="formLoading">发货</el-button>
        </div>
    </el-dialog>
</template>

<script type="es6">
    import util from '../../../api/util'
    import {httpGet, httpPost} from '../../../api/api';

    export default {
        name: 'vDetail',
        props: ['value', 'data'],
        data() {
            let checkNum = (rule, value, callback) => {
                if (!Number.isInteger(value)) {
                    callback(new Error('手机号必须为整数'));
                } else if (value.length != 11) {
                    callback(new Error('手机号格式错误'));
                } else {
                    callback();
                }
            };
            return {
                visible: false, //默认隐藏
                showLoading: false,
                formTitle: '需改订单信息',
                formLoading: false,
                formRules: {
                    username: [
                        {required: true, message: '请填写用户名', trigger: 'blur'},
                    ],
                    phone: [
                        {required: true, message: '请填写手机号', trigger: 'blur'},
                        {validator: checkNum, trigger: 'blur'}
                    ],
                    area1: [
                        {required: true, message: '请填写省', trigger: 'blur'},
                    ],
                    // area2: [
                    //     {required: true, message: '请填写市', trigger: 'blur'},
                    // ],
                    area3: [
                        {required: true, message: '请填写区', trigger: 'blur'},
                    ],
                    address: [
                        {required: true, message: '请填写住址', trigger: 'blur'},
                    ],
                },
                formData: {
                    id: '',
                    productName: '',
                    productId: '',
                    style: '',
                    styleId: '',
                    size: '',
                    sizeId: '',
                    adressResponse: {
                        id:'',
                        username: '',
                        phone: '',
                        address: '',
                        area1: '',
                        area2: '',
                        area3: '',
                    },
                },
                inputVisible: false,
                inputValue: '',
                avatarLoading: false,
                styleList: [],
                sizeList: [],
            }
        },
        computed: {
            detail() { //返回详情
                let _self = this;
                let productId = _self.data.productId;
                if (!productId) {
                    return;
                }
                _self.formData = _self.data;
                //查询商品信息
                httpGet('productDetail', {id: productId}, _self, function (res) {
                    _self.tableLoading = false;
                    let {error, status, data} = res;
                    //商品样式列表赋值
                    if(data.sizeNames){
                        _self.sizeList = data.sizeNames.map(o=>{
                            o.id=o.id+''
                            return o;
                        });
                    }else {
                        _self.sizeList=null;
                    }
                    //商品尺寸列表赋值
                    if(data.sizeNames){
                        _self.styleList = data.styleNames.map(o=>{
                            o.id=o.id+''
                            return o;
                        });
                    }else {
                        _self.styleList=null;
                    }

                    //由于服务端问题无法回显，所以需要用尺寸和样式Name匹配id
                //     if (_self.sizeList) {
                //         _self.sizeList.map(o=>{
                //             if (o.name === _self.formData.size) {
                //                 _self.formData.sizeId = o.id+'';
                //                 return;
                //             }
                //         });
                //     }
                //     if (_self.styleList) {
                //             _self.styleList.map(o=>{
                //             if (o.name === _self.formData.style) {
                //                 _self.formData.styleId = o.id+'';
                //                 return;
                //             }
                //         })
                //     }
                });
            }
        },
        methods: {
            formSubmit() { //提交表单
                let _self = this;

                let para = new Object();
                para.id = _self.formData.id;
                para.sizeId = _self.formData.sizeId;
                para.styleId = _self.formData.styleId;
                para.username = _self.formData.adressResponse.username;
                para.phone = _self.formData.adressResponse.phone;
                para.area1 = _self.formData.adressResponse.area1;
                para.area2 = _self.formData.adressResponse.area2;
                para.area3 = _self.formData.adressResponse.area3;
                para.address = _self.formData.adressResponse.address;
                para.addressId = _self.formData.adressResponse.id;

                _self.$refs.formData.validate((valid) => {
                    if (valid) {
                        _self.formLoading = true;
                        httpPost('orderUpdate', para, _self, function (res) {
                            _self.formLoading = false;
                            try {
                                _self.$message.success('修改成功');
                                _self.resetFormData();
                                _self.visible = false;
                                _self.$emit('refresh');
                            } catch (error) {
                                util.jsErrNotify(error);
                            }
                        });
                    }
                });
            },
            resetFormData() { //关闭表格弹窗，重置表格数据
                let _self = this;
                _self.formData =  {
                    id: '',
                    productName: '',
                    productId: '',
                    style: '',
                    styleId: '',
                    size: '',
                    sizeId: '',
                    adressResponse: {
                        id:'',
                        username: '',
                        phone: '',
                        address: '',
                        area1: '',
                        area2: '',
                        area3: '',
                    },
                };
            }
        },
        watch: {
            detail(val) { //监测详情变化
            },
            value(val) {
                this.visible = val;
            },
            visible(val) {
                this.$emit('input', val);
            }
        },
        mounted() {
            if (this.value) {
                this.visible = true;
            }
        }
    }
</script>



