<template>
    <section>
        <!--顶部工具条-->
        <el-col :span="24" class="toolbar">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.orderNo" placeholder="请输入订单号" icon="circle-close"
                              :on-icon-click="resetSearch"
                              @keyup.enter.native="fetchList"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-select v-model="filters.status" @change="fetchList" placeholder="请选择" style="width: 150px;">
                        <el-option label="待支付" value="1"></el-option>
                        <el-option label="待发货" value="2"></el-option>
                        <el-option label="已发货" value="3"></el-option>
                        <el-option label="已完成" value="0"></el-option>
                        <el-option label="交易关闭" value="-1"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="fetchList()">查询</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="exportOrder">导出</el-button>
                </el-form-item>
            </el-form>
        </el-col>

        <!--表格-->
        <el-table v-loading="tableLoading" class="table-expand" :data="tableList" stripe border
                  :max-height="tableHeight" style="width: 100%;">

            <el-table-column prop="orderNo" label="订单号" width="100"></el-table-column>
            <el-table-column prop="createTime" label="创建时间" width="180"></el-table-column>
            <el-table-column prop="productName" label="商品名称"></el-table-column>
            <el-table-column prop="productId" label="商品ID"></el-table-column>

            <el-table-column label="订单信息" width="200">
                <template scope="scope">
                    数量: {{ scope.row.number }},
                    {{ scope.row.style }},
                    {{ scope.row.size }}
                    <br>

                    {{ scope.row.adressResponse.username }},
                    {{ scope.row.adressResponse.phone }}
                    <br>
                    {{ scope.row.adressResponse.area1 }}
                    {{ scope.row.adressResponse.area2 }}
                    {{ scope.row.adressResponse.area3 }}
                    {{ scope.row.adressResponse.address }}

                </template>
            </el-table-column>

            <el-table-column label="订单总金额" prop="sumPrice">
                <template scope="scope">
                    ¥{{ scope.row.sumPrice}}
                </template>
            </el-table-column>

            <el-table-column label="订单状态" prop="status">
                <template scope="scope">
                    <span v-if="scope.row.status==1">待支付</span>
                    <span v-else-if="scope.row.status==2">代发货</span>
                    <span v-else-if="scope.row.status==3">已发货</span>
                    <span v-else-if="scope.row.status==0">已完成</span>
                    <span v-else-if="scope.row.status==-1">交易关闭</span>
                </template>
            </el-table-column>

            <el-table-column label="物流信息">
                <template scope="scope">
                    {{scope.row.expressName}}<br>{{scope.row.expressNo}}
                </template>
            </el-table-column>

            <el-table-column label="订单人">
                <template scope="scope">
                    <img v-if="scope.row.avatar != ''" style="width: 100%;vertical-align:middle;margin:10px auto;"
                         :src="scope.row.avatar" alt=""/>
                    <br>
                    {{scope.row.nickname}}
                </template>
            </el-table-column>

            <el-table-column label="操作" width="200">
                <template scope="scope">
                    <el-button-group class="mt-10">

                        <el-button size="small" type="danger" @click="cancelTran(scope.row)" v-if="scope.row.status==1">
                            交易关闭
                        </el-button>

                        <el-button size="small" type="danger" @click="sureReceive(scope.row)"
                                   v-if="scope.row.status==3">确认收货
                        </el-button>

                        <el-button size="small" type="danger" @click="saleLog(scope.row)" v-if="scope.row.status==0">
                            售后记录
                        </el-button>

                        <el-button size="small" type="danger" @click="updateOrder(scope.row)"
                                   v-if="scope.row.status==2">修改订单信息
                        </el-button>

                        <el-button size="small" type="danger" @click="sendProduct(scope.row)"
                                   v-if="scope.row.status==2">发货
                        </el-button>

                    </el-button-group>
                </template>
            </el-table-column>
        </el-table>

        <!--工具条-->
        <el-col :span="24" class="mt-10">
            <el-pagination style="float:right;" @current-change="handleCurrentChange"
                           :page-size="10" :current-page="page"
                           layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </el-col>

        <!--修改订单信息-->
        <v-detail :data="data" v-model="updateOrderShow" v-on:refresh="fetchList"></v-detail>

        <!--填写物流信息-->
        <el-dialog :visible.sync="sendProductShow" title="填写物流信息">
            <el-form :model="expressInfo" label-width="120px" ref="expressInfo" :rules="expressInfoRules">

                <el-form-item label="快递公司" prop="expressId" required>
                    <el-select placeholder="请选择快递公司" v-model="expressInfo.expressId">
                        <el-option :label="item.name" :value="item.id" v-for="item in expressList"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="快递单号" prop="expressNo" required>
                    <el-input v-model="expressInfo.expressNo" placeholder="快递单号"></el-input>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="small" @click="sendProductShow = false">取消</el-button>
                <el-button size="small" type="primary" @click.native="sendProductSubmit">发货</el-button>
            </div>
        </el-dialog>

        <!--售后记录查询与提交-->
        <el-dialog :visible.sync="saleLogShow" title="售后记录">
            <el-input v-model="saleLogData.currentSaleLog" auto-complete="off" placeholder="请填写售后记录"></el-input>
            <br>
            <div v-for="item in saleLogList">
                {{item.createTime}}<br>
                {{item.log}}<br><br>
            </div>

            <div slot="footer" class="dialog-footer">
                <el-button size="small" @click="saleLogShow = false">取消</el-button>
                <el-button size="small" type="primary" @click.native="saleLogSubmit">确定</el-button>
            </div>
        </el-dialog>

        <!--导出-->
        <el-dialog :visible.sync="exportOrderShow" title="导出订单">
            <el-form :model="exportInfo" label-width="120px" ref="exportInfo" :rules="exportOrderRules">
                <el-form-item required label="订单状态" prop="status">
                    <el-select v-model="exportInfo.status" placeholder="请选择" style="width: 150px;">
                        <el-option label="待支付" value="1"></el-option>
                        <el-option label="代发货" value="2"></el-option>
                        <el-option label="已发货" value="3"></el-option>
                        <el-option label="已完成" value="0"></el-option>
                        <el-option label="交易关闭" value="-1"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item required label="开始时间" prop="startTime">
                    <el-date-picker
                        v-model="exportInfo.startTime"
                        type="date"
                        placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>

                <el-form-item required label="结束时间" prop="endTime">
                    <el-date-picker
                        v-model="exportInfo.endTime"
                        type="date"
                        placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button size="small" @click.native="exportOrderShow=false">取消</el-button>
                <el-button size="small" type="primary" @click.native="exportOrderSubmit">确定</el-button>
            </div>
        </el-dialog>
    </section>
</template>

<script type="es6">
    import util, {formatDate} from '../../../api/util'
    import {httpGet, httpPost} from '../../../api/api';
    import vDetail from './orderDetail.vue'

    export default {
        components: {
            vDetail
        },
        data() {
            return {
                exportOrderRules: {
                    status: [
                        {required: true, message: '请选择订单状态', trigger: 'blur'},
                    ],
                },
                expressInfoRules: {
                    expressNo: [
                        {required: true, message: '请填写快递单号', trigger: 'blur'},
                    ],
                },
                filters: {
                    orderNo: '',
                    status: ''
                },
                total: 0, //表格列表数据总数
                page: 1, //当前页，默认为第一页
                tableHeight: '100%',
                tableLoading: false, //表格的loading符号
                tableList: [], //表格数据
                data: {},
                saleLogShow: false,
                updateOrderShow: false,
                sendProductShow: false,
                exportOrderShow: false,
                saleLogList: [],
                saleLogData: {
                    id: '',
                    currentSaleLog: '',
                },
                saleLogList: [],
                expressInfo: {
                    id: '',
                    expressId: '',
                    expressNo: '',
                },
                exportInfo: {
                    status: '',
                    startTime: '',
                    endTime: '',
                },
                expressList: [],
            }
        },
        methods: {
            handleCurrentChange(val) { //翻页
                this.page = val;
                this.fetchList();
            },
            fetchList() { //获取列表
                let _self = this;
                _self.tableHeight = document.getElementById('container').clientHeight - 77 - 42 - 15;
                let para = {
                    orderNo: _self.filters.orderNo,
                    offset: 0,
                    size: 10,
                    status: _self.filters.status
                };
                para.offset = (_self.page - 1) * para.size;
                _self.tableLoading = true;
                httpGet('orderList', para, _self, function (res) {
                    _self.tableLoading = false;
                    try {
                        let {error, status, data} = res;
                        _self.total = data.totalElements;
                        _self.tableList = data.content.map(o => {
                            if (o.style) {
                                o.styleId = o.style.id + '';
                                o.style = o.style.name;
                            }
                            if (o.size) {
                                o.sizeId = o.size.id + '';
                                o.size = o.size.name;
                            }
                            let ar = o.adressResponse;
                            if (ar) {
                                o.adressResponse = ar;
                            } else {
                                o.adressResponse = {
                                    "id": "",
                                    "username": "",
                                    "phone": "",
                                    "address": "",
                                    "area1": "",
                                    "area2": "",
                                    "area3": ""
                                };
                            }
                            return o;
                        });
                    } catch (error) {
                        util.jsErrNotify(error);
                    }
                });
            },
            resetSearch() {
                this.filters = {
                    orderNo: '',
                    status: ''
                }
                this.fetchList();
            },
            getExpressList() {
                let _self = this;
                httpGet('expressList', {}, _self, function (res) {
                    try {
                        let {error, status, data} = res;
                        _self.expressList = data;
                    } catch (error) {
                        util.jsErrNotify(error);
                    }
                });
            },
            exportOrder(row) {
                let _self = this;
                _self.exportOrderShow = true;
            },
            cancelTran(row) {
                let _self = this;
                _self.$confirm('是否关闭此订单?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let para = new Object();
                    para.id = row.id;
                    httpPost('orderClose', para, _self, function (res) {
                        try {
                            _self.$message.success('操作成功');
                            _self.fetchList();
                        } catch (error) {
                            util.jsErrNotify(error);
                        }
                    });
                }).catch(() => {
                });
            },
            sureReceive(row) {
                let _self = this;
                _self.$confirm('是否将订单标记为已确认收货?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let para = new Object();
                    para.id = row.id;
                    httpPost('orderConfirm', para, _self, function (res) {
                        try {
                            _self.$message.success('操作成功');
                            _self.fetchList();
                        } catch (error) {
                            util.jsErrNotify(error);
                        }
                    });
                }).catch(() => {
                });
            },
            saleLog(row) {
                let _self = this;
                let para = new Object();
                _self.saleLogData = {
                    id: row.id,
                    currentSaleLog: '',
                }
                para.id = row.id;
                //售后记录列表查询
                httpGet('orderListLog', para, _self, function (res) {
                    try {
                        let {error, status, data} = res;
                        _self.saleLogList = data;
                    } catch (error) {
                        util.jsErrNotify(error);
                    }
                });
                _self.saleLogShow = true;
            },
            updateOrder(row) {
                let _self = this;
                _self.data = row;
                _self.updateOrderShow = true;

            },
            sendProduct(row) {
                let _self = this;
                _self.expressInfo.id = row.id;
                _self.expressInfo.expressNo = row.expressNo;
                _self.sendProductShow = true;
            },
            // 提交系列
            saleLogSubmit() {
                let _self = this;
                if (_self.currentSaleLog === '') {
                    _self.$message.error('请填写售后记录');
                    return;
                }
                let para = new Object();
                para.id = _self.saleLogData.id;
                para.record = _self.saleLogData.currentSaleLog;
                httpPost('orderLog', para, _self, function (res) {
                    try {
                        _self.$message.success('提交成功');
                        _self.saleLogShow = false;
                        _self.saleLogData = {
                            id: '',
                            currentSaleLog: '',
                        }
                    } catch (error) {
                        util.jsErrNotify(error);
                    }
                });
            },
            sendProductSubmit() {
                let _self = this;
                if (!_self.expressInfo.expressId) {
                    _self.$message.error('请选择快递公司');
                    return;
                }
                let para = new Object();
                para.id = _self.expressInfo.id;
                para.expressId = _self.expressInfo.expressId;
                para.expressNo = _self.expressInfo.expressNo;
                _self.$refs.expressInfo.validate((valid) => {
                    if (valid) {
                        httpPost('orderSend', para, _self, function (res) {
                            try {
                                _self.$message.success('提交成功');
                                _self.sendProductShow = false;
                                _self.expressInfo = {
                                    id: '',
                                    expressId: '',
                                    expressNo: '',
                                };
                                _self.fetchList();
                            } catch (error) {
                                util.jsErrNotify(error);
                            }
                        });
                    }
                });
            },
            exportOrderSubmit() {
                let _self = this;
                let startTime = _self.exportInfo.startTime;
                let endTime = _self.exportInfo.endTime;
                if (startTime === '' || endTime === '') {
                    _self.$message.error('请选择时间');
                    return;
                }
                startTime = Date.parse(startTime);
                endTime = Date.parse(endTime);
                console.log(startTime + "  " + endTime);
                if (endTime - startTime > 24 * 60 * 60 * 60 * 1000) {
                    _self.$message.error('日期间隔不能大于60天');
                    return;
                }
                _self.$refs.exportInfo.validate((valid) => {
                    if (valid) {
                        let startTime = formatDate(_self.exportInfo.startTime, 'yyyy-MM-dd hh:mm:ss');
                        let endTime = formatDate(_self.exportInfo.endTime, 'yyyy-MM-dd hh:mm:ss');
                        window.open(location.origin + '/shop/order/export?startTime=' + startTime + '&endTime=' + endTime + '&status=' + _self.exportInfo.status);
                        _self.exportOrderShow = false;
                        _self.exportInfo = {
                            type: '',
                            startTime: '',
                            endTime: '',
                        };
                    }
                });
            }
        },
        mounted() {
            this.fetchList();
            this.getExpressList();
        }
    }
</script>

<style>
    /*
        表格展开部分样式
    */
    .table-expand label {
        width: 90px;
        color: #99a9bf;
    }

    .table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 100%;
    }

</style>



